import React from 'react';
import { IonContent, IonPage, IonList, IonLabel, IonSelect, IonSelectOption, IonItem, IonDatetime } from '@ionic/react';
import { Plugins } from '@capacitor/core';
import Header from './sub-components/header';
import notifications from '../components/Notifications';

const { Storage } = Plugins;

interface MyState {
  language: string,
  prayerTime: null | string | undefined
}

class Settings extends React.Component<{}, MyState> {
  constructor(props: any) {
    super(props);
    this.state = { language: 'en', prayerTime: null };
    this.retrieveSettings();
  }

  retrieveSettings() {
    this.retrieveLanguage();
    this.retrieveprayerTime();
  }
  
  retrieveLanguage() {
    Storage.get({ key: 'language' }).then((data) => {
      const langValue = data.value || 'en';
      this.setState({language: langValue})
    })
  }
  
  retrieveprayerTime() {
    Storage.get({ key: 'prayerTime' }).then((data) => {
      const value = data.value || null;
      this.setState({prayerTime: value})
    })
  }

  updateLanguage(value: string) {
    Storage.set({
      key: 'language',
      value: value
    }).then((data) => {
      const langValue = value || 'en';
      this.setState({language: langValue})
    })
  }

  updatePrayerTime(value: string) {
    Storage.set({
      key: 'prayerTime',
      value: value
    }).then((data) => {
      this.setState({prayerTime: value})
      this.setLocalNotification(value)
    })
  }
  
  setLocalNotification(time: string) {
    const parsedTime = new Date(time);
    notifications.schedule(parsedTime.getHours(), parsedTime.getMinutes())
  }

  render() {
    return (
      <IonPage>
        <Header title='Settings' enableBack={true} />

        <IonContent fullscreen>
          <IonList>
            <IonItem>
              <IonLabel>Prayer Time</IonLabel>
              <IonDatetime name='prayerTime' displayFormat="HH:mm" value={this.state.prayerTime} onIonChange={e => this.updatePrayerTime(e.detail.value as string) }></IonDatetime>
            </IonItem>

            <IonItem>
              <IonLabel>Language</IonLabel>
              <IonSelect value={this.state.language} placeholder="Select One" onIonChange={e => this.updateLanguage(e.detail.value)}>
                <IonSelectOption value="en">English</IonSelectOption>
                <IonSelectOption value="ko">Korean</IonSelectOption>
                <IonSelectOption value="sp">Spanish</IonSelectOption>
                <IonSelectOption value="zh">Taiwanese</IonSelectOption>
                <IonSelectOption value="zh_cn">Chinese</IonSelectOption>
              </IonSelect>
            </IonItem>
          </IonList>
        </IonContent>
      </IonPage>
    );
  }
}

export default Settings;
