import { Plugins } from '@capacitor/core';
const { LocalNotifications } = Plugins;

class Notifications {
  public async schedule(hour: number, minute: number) {
    try {
      if (!(await LocalNotifications.requestPermission()).granted) {
        alert('Permission failed')
        return;
      }
      alert(`Notification time - ${hour}:${minute}`)
      const pending = await LocalNotifications.getPending();
      if (pending.notifications.length > 0)
        await LocalNotifications.cancel(pending);

      const schNotif = await LocalNotifications.schedule({
        notifications: [{
          title: "Daily prayer",
          body: "A gentle reminder about your daily prayer",
          id: Math.floor(Math.random() * 1000 + 1),
          schedule: {
            on: {
              hour,
              minute
            }
          }
        }]
      });
      alert(schNotif);
    } catch (error) {
      console.error(error);
      alert(error);
    }
  }
}

export default new Notifications()