import React from 'react';
import { IonHeader, IonTitle, IonToolbar, IonButtons, IonMenuButton, IonImg, IonRow, IonThumbnail, IonButton, IonIcon } from '@ionic/react';
import '../../theme/custom_overrides.scss';
import { arrowBackOutline } from 'ionicons/icons';


interface MyProps {
  title: string,
  enableBack: boolean,
  backUrl?: string
}

class Header extends React.Component<MyProps, {}> {

  setupBackBtn() {
    if (this.props.enableBack) {
      return(
        <IonButton color='light' onClick={() => {window.history.back()}}>
          <IonIcon slot="icon-only" icon={arrowBackOutline} />
        </IonButton>
      )
    }
  }

  render() {
    return (
      <IonHeader>
        <IonToolbar color='blue'>
          <IonButtons slot="start">
            {this.setupBackBtn()}
          </IonButtons>
          <IonTitle>
            <IonRow class="ion-justify-content-center onboard-image">
              <div>
                <IonThumbnail>
                  <IonImg src='assets/images/logo-white.png' class='header-logo' />
                </IonThumbnail>
              </div>
            </IonRow>
          </IonTitle>
          <IonButtons slot="end">
            <IonMenuButton color='light' autoHide={false} />
          </IonButtons>
        </IonToolbar>
      </IonHeader>
    );
  }
}

export default Header;
