import React from 'react';
import { IonAvatar, IonHeader, IonTitle, IonToolbar, IonMenu, IonContent, IonIcon, IonItem, IonLabel, IonList } from '@ionic/react';
import { settings, shareSocial } from 'ionicons/icons';
import '../../theme/variables.scss';
import { Plugins } from '@capacitor/core';


interface MyProps {
}

const shareAppInfo = async () => {
  const { Share } = Plugins;

  await Share.share({
    title: 'Beseeching Mobile',
    text: 'Intercessory Prayer in North America Beseeching.org has been redesigned and relaunched to help the saints in North America participate in coordinated prayer for the Lord&#8217;s move as a continuation of the 60 days of global prayer using unceasinglypray.org. The burden of beseeching.org is to afford the Lord the way to continue building up the prayer',
    url: 'https://beseeching.org/',
  });
}

class SideMenu extends React.Component<MyProps, {}> {
  render() {
    return (
      <IonMenu side="end" menuId="first" contentId='content' id='sidemenu'>
        <IonHeader>
          <IonToolbar>
            <IonTitle class='ion-text-center'>MENU</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonContent>
          <IonList>
            <IonItem routerLink='/settings'>
              <IonAvatar color='blue' class='ion-align-items-center' slot="start">
                <IonIcon color='blue' icon={settings}></IonIcon>
              </IonAvatar>
              <IonLabel>SETTINGS</IonLabel>
            </IonItem>
            <IonItem button onClick={() => shareAppInfo()}>
              <IonAvatar color='blue' class='ion-align-items-center' slot="start">
                <IonIcon color='blue' icon={shareSocial} />
              </IonAvatar>
              <IonLabel>SHARE</IonLabel>
            </IonItem>            
          </IonList>
        </IonContent>
      </IonMenu>
    );
  }
}

export default SideMenu;
