import React from 'react';
import parse from 'html-react-parser';
import { IonContent, IonGrid, IonPage, IonText } from '@ionic/react';
import Header from './sub-components/header';

const apiUrl = 'https://beseeching.org/wp-json/wp/v2/pages/950';

interface WpPage {
  id: string,
  date: string,
  status: string,
  type: string,
  title: string,
  content: string,
  excerpt: string,
}

interface MyProps {
  history: object,
  location: object,
  match: any,
  staticContext: string
}

class Study extends React.Component<MyProps, WpPage> {
  constructor(props: any) {
    super(props);
    this.state = {id: '', date: '', status: '', type: '', title: '', content: '', excerpt: ''};
  }

  componentDidMount() {
    this.fetchPost();
  }

  async fetchPost() {
    const response = await fetch(apiUrl);
    const retrievedPost = await response.json();

    const stateObj = {
      id: retrievedPost.id,
      date: retrievedPost.date,
      status: retrievedPost.status,
      type: retrievedPost.type,
      title: retrievedPost.title.rendered,
      content: retrievedPost.content.rendered,
      excerpt: retrievedPost.excerpt.rendered
    }
    this.setState(stateObj)
  }

  render() {
    return (
      <IonPage id='study'>
        <Header title='Study' enableBack={false} />
        <IonContent fullscreen>
          <IonGrid class='ion-padding'>
            <IonText>
              {parse(this.state.content)}
            </IonText>
          </IonGrid>          
        </IonContent>
      </IonPage>
    );
  }
}

export default Study;
