import React from 'react';
import { IonContent, IonPage, IonList, IonRefresher, IonRefresherContent, IonInfiniteScroll, IonInfiniteScrollContent, IonSkeletonText, IonItemDivider } from '@ionic/react';
import { RefresherEventDetail } from '@ionic/core';
import { Plugins } from '@capacitor/core';
import Post from './sub-components/post'; 
import Header from './sub-components/header';
import './DailyPrayer.css';

const { Storage } = Plugins;
const apiHost = 'https://beseeching.org'
const postPerPage = 6
const dailyPrayerUrl = '/wp-json/wp/v2/posts?categories=6';

interface MyState {
  posts: any[],
  lang: string,
  page: number,
  totalPages: number,
  allContentLoaded: boolean
}

class DailyPrayer extends React.Component<{}, MyState> {
  constructor(props: object) {
    super(props);
    this.state = {posts: [], lang: 'en', page: 0, allContentLoaded: false, totalPages: 0};
  }
  
  componentDidMount() {
    Storage.get({ key: 'language' }).then((data) => {
      const langValue = data.value || 'en';
      this.setState({lang: langValue});
      this.fetchPosts(langValue);
    })
  }
  
  doRefresh(event: CustomEvent<RefresherEventDetail>) {
    this.setState({page: 0, allContentLoaded: false, posts: []});
    this.fetchPosts(this.state.lang);
    
    setTimeout(() => {
      event.detail.complete();
    }, 1000);
  }
  
  fetchPosts(language: string) {
    if (this.state.allContentLoaded) {
      return;
    }
    const pageToBeFetched = this.state.page + 1
    let url = (language === 'en')? `${apiHost}${dailyPrayerUrl}`: `${apiHost}/${language}${dailyPrayerUrl}`
    url = url + `&page=${pageToBeFetched}&per_page=${postPerPage}`
    
    const req = fetch(url).then((response) => {
      response.headers.get('X-WP-TotalPages')
      response.json().then((retrievedPosts) => {
        const totalPages = parseInt(response.headers.get('X-WP-TotalPages'));
        let updatedPosts = this.state.posts.concat(retrievedPosts)
        this.setState({posts: updatedPosts, page: pageToBeFetched, totalPages: totalPages, allContentLoaded: (totalPages === pageToBeFetched)});
      })
    })
    return req;
  }

  buildPosts(posts: any[]) {
    const postsListing = posts.map(function (post){
      return <Post post={post} category='daily_prayer' key={post.id}/>
    })
    return postsListing
  }

  loadPosts(e) {
    this.fetchPosts(this.state.lang).then(() => {
      e.target.complete();
    })
  }
  
  dummyPostsList() {
    const dummyPost = <div>
        <h2><IonSkeletonText animated style={{ width: '30%' }} /></h2>
        <div>
          <p><IonSkeletonText animated style={{ width: '80%' }} /></p>
          <p><IonSkeletonText animated style={{ width: '65%' }} /></p>
        </div>
        <IonItemDivider></IonItemDivider>
      </div>
    return([dummyPost, dummyPost, dummyPost, dummyPost])
  }
  
  render() {
    const dailyPosts = (this.state.posts.length > 0)? this.buildPosts(this.state.posts) : ''

    return (
      <IonPage>
        <Header title='Daily Prayer' enableBack={false} />
        <IonContent fullscreen>
          <IonRefresher slot="fixed" onIonRefresh={this.doRefresh.bind(this)} >
            <IonRefresherContent pullingText="Pull to refresh" refreshingSpinner="circles" refreshingText="Refreshing..."></IonRefresherContent>
          </IonRefresher>
          <IonList>
            {(this.state.posts.length > 0)? dailyPosts : this.dummyPostsList()}
          </IonList>
          <IonInfiniteScroll onIonInfinite={(e: CustomEvent<void>) => this.loadPosts(e)}>
            <IonInfiniteScrollContent loadingSpinner="bubbles" loadingText="Loading previous daily prayer posts ...">
            </IonInfiniteScrollContent>
          </IonInfiniteScroll>
        </IonContent>
      </IonPage>
    );
  }
}

export default DailyPrayer;
