import React from 'react';
import parse from 'html-react-parser';
import { IonGrid, IonItem, IonRow } from '@ionic/react';
import '../../theme/variables.scss';

class Post extends React.Component<any, {}> {

  formatedDate() {
    const options = { weekday: 'short', year: 'numeric', month: 'long', day: 'numeric' };
    const date = new Date(this.props.post.date);
    return date.toLocaleDateString("en-US", options)
  }

  postUrl() {
    return `/post/${this.props.post.id}`
  }

  postTitle() {
    const title = this.props.post.title.rendered.split(':')
    return title[0]
  }

  render(){
    return(
      <IonItem key={this.props.post.id} routerLink={this.postUrl()}>
        <IonGrid>
          <IonRow>
            <h2 className='post-title'>{this.postTitle()}</h2>
          </IonRow>
          <IonRow>
            <div className='post-content'>{parse(this.props.post.excerpt.rendered)}</div>
          </IonRow>
        </IonGrid>
      </IonItem>
    )
  }
}
  
export default Post;
