import React, { useEffect } from "react";
import {
  IonApp, IonIcon, IonLabel, IonRouterOutlet, IonTabBar, IonTabButton, IonTabs} from '@ionic/react';
import { IonReactRouter } from '@ionic/react-router';

/* Views list */
import SideMenu from './pages/sub-components/sidemenu';

/* Core CSS required for Ionic components to work properly */
import '@ionic/react/css/core.css';

/* Basic CSS for apps built with Ionic */
import '@ionic/react/css/normalize.css';
import '@ionic/react/css/structure.css';
import '@ionic/react/css/typography.css';

/* Optional CSS utils that can be commented out */
import '@ionic/react/css/padding.css';
import '@ionic/react/css/float-elements.css';
import '@ionic/react/css/text-alignment.css';
import '@ionic/react/css/text-transformation.css';
import '@ionic/react/css/flex-utils.css';
import '@ionic/react/css/display.css';

/* Theme variables */
import './theme/variables.scss';
import './theme/fonts.css';
import './theme/custom_overrides.scss';
import './theme/main.scss';
import { Route, Redirect } from "react-router";
import DailyPrayer from "./pages/DailyPrayer";
import OnBoarding from "./pages/OnBoarding";
import PostShow from "./pages/PostShow";
import Settings from "./pages/Settings";
import SpecialBurdens from "./pages/SpecialBurdens";
import Study from "./pages/Study";
import { Capacitor, Plugins } from "@capacitor/core";

function backHandler() {
  const rootPaths = ['/Study', '/DailyPrayer', '/SpecialBurdens']
  if (Capacitor.isNative) {
    Plugins.App.addListener("backButton", (e) => {
      if (window.location.pathname === "/") {
        let ans = window.confirm("Are you sure");
        if (ans) {
          Plugins.App.exitApp();
        } 
      } else if (rootPaths.includes(window.location.pathname)) {
        let ans = window.confirm("Are you sure");
        if (ans) {
          Plugins.App.exitApp();
        } 
      } 
    });
  }
}

const App: React.FC = () => (
  // useEffect(backHandler(), []);
  <IonApp>
    <SideMenu/>
    <IonReactRouter>
      <IonTabs>
        <IonRouterOutlet id='content'>
          <Route path="/Study" component={Study} exact={true} />
          <Route path="/DailyPrayer" component={DailyPrayer} exact={true} />
          <Route path="/SpecialBurdens" component={SpecialBurdens} exact={true} />
          <Route path="/settings" component={Settings} exact={true} />
          <Route path="/onboarding" component={OnBoarding} exact={true} />
          <Route path="/post/:id" component={PostShow} exact={true} />
          <Route path="/" render={() => <Redirect to="/onboarding" />} exact={true} />
        </IonRouterOutlet>
        <IonTabBar slot="bottom">
          <IonTabButton tab="SpecialBurdens" href="/SpecialBurdens">
            <IonIcon src='assets/icon/pray.svg' />
            <IonLabel>BURDENS</IonLabel>
          </IonTabButton>
          <IonTabButton tab="DailyPrayer" href="/DailyPrayer">
            <IonIcon src='assets/icon/burdens.svg' />
            <IonLabel>PRAY</IonLabel>
          </IonTabButton>
          <IonTabButton tab="Study" href="/Study">
            <IonIcon src='assets/icon/study.svg' />
            <IonLabel>STUDY</IonLabel>
          </IonTabButton>
        </IonTabBar>
      </IonTabs>

    </IonReactRouter>
  </IonApp>
);

export default App;
