import React from 'react';
import parse from 'html-react-parser';
import { IonContent, IonGrid, IonPage, IonSkeletonText, IonText } from '@ionic/react';
import Header from './sub-components/header';
import { Plugins } from '@capacitor/core';
import '../theme/variables.scss';

const { Storage } = Plugins;
const apiHost = 'https://beseeching.org'
const postUrl = '/wp-json/wp/v2/posts/';

interface WpPost {
  id: string,
  date: string,
  status: string,
  type: string,
  title: string,
  content: string,
  excerpt: string,
}

interface MyProps {
  history: object,
  location: object,
  match: any,
  staticContext: string
}

class PostShow extends React.Component<MyProps, WpPost> {
  constructor(props: any) {
    super(props);
    this.state = {id: '', date: '', status: '', type: '', title: '', content: '', excerpt: ''};
  }

  componentDidMount() {
    const id = this.props.match.params.id;
    Storage.get({ key: 'language' }).then((data) => {
      const langValue = data.value || 'en';
      this.fetchPosts(id, langValue);
    })
  }

  fetchPosts(id: number, language: string) {
    const url = (language === 'en')? `${apiHost}${postUrl}${id}`: `${apiHost}/${language}${postUrl}${id}`

    fetch(url).then((response) => {
      response.json().then((retrievedPost) => {
        const stateObj = {
          id: retrievedPost.id,
          date: retrievedPost.date,
          status: retrievedPost.status,
          type: retrievedPost.type,
          title: retrievedPost.title.rendered,
          content: retrievedPost.content.rendered,
          excerpt: retrievedPost.excerpt.rendered
        }
        this.setState(stateObj)
      })
    })
  }

  title() {
    console.log(this.props.history);

    if (this.state.title === '') {
      return null;
    }
    const divergedTitle = this.state.title.split(':');
    return(<h2 id='postTitle' className='post-title'><span className='day-info'>{divergedTitle[0]}:</span> <span>{divergedTitle[1]}</span></h2>)
  }

  render() {
    return (
      ((this.state.id === '')? 
        (    
          <IonPage id='post-show'>
            <Header title={this.state.title} enableBack={true} />
            <IonContent>
              <IonGrid class='ion-padding'>
                <IonText>
                <h2><IonSkeletonText animated style={{ width: '50%' }} /></h2>
                <p><IonSkeletonText animated style={{ width: '90%' }} /></p>
                <p><IonSkeletonText animated style={{ width: '60%' }} /></p>
                <p><IonSkeletonText animated style={{ width: '70%' }} /></p>
                <h2><IonSkeletonText animated style={{ width: '40%' }} /></h2>
                <p><IonSkeletonText animated style={{ width: '100%' }} /></p>
                </IonText>
              </IonGrid>
            </IonContent>
          </IonPage>
        ) : (
          <IonPage id='post-show'>
            <Header title={this.state.title} enableBack={true} />
            <IonContent>
              <IonGrid class='ion-padding'>
                <IonText>
                  {this.title()}
                  {parse(this.state.content)}
                </IonText>
              </IonGrid>
            </IonContent>
          </IonPage>
        )
      )
    );
  }
}

export default PostShow;
