import React from 'react';
import { IonButton, IonContent, IonGrid, IonImg, IonLoading, IonPage, IonRow, IonSlide, IonSlides } from '@ionic/react';
import Header from './sub-components/header';
import { Plugins } from '@capacitor/core';
import { Redirect } from 'react-router';
import { getValue, setValue } from "../components/storage";
const { SplashScreen } = Plugins;

interface MyState {
  new_user: string|boolean|null
}
interface MyProps {
  history: History
}

const slideOpts = {
  initialSlide: 0,
  speed: 400
};

class OnBoarding extends React.Component<MyProps, MyState> {
  constructor(props: any){
    super(props);
    this.state = { new_user: null };
  }
  
  componentDidMount(){
    getValue('new_user').then((response) => {
      const val = (response === null)? true : response
      this.setState({new_user: val})
    })
    // this.toggleTabbar('hidden')
  }
  
  performOnboardingCompletion() {
    setValue('new_user', false).then(() => {
      this.setState({'new_user': false})
    })
    return ;
  }
  
  toggleTabbar(display: string) {
    const tabBar = document.querySelectorAll('ion-tab-bar')
    const menu = document.querySelectorAll('ion-header > ion-toolbar > ion-buttons')
    if (tabBar && menu) {
      [].concat(Array.from(menu), Array.from(tabBar)).forEach((el) => { el.style.visibility = display; })
    }
  }
  
  render() {
    if (this.state.new_user === null) {
      return(<IonLoading isOpen={!this.state.new_user} message={'Please wait...'} duration={5000}/>)
    }
    SplashScreen.hide();
    if(this.state.new_user === false) {
      this.toggleTabbar('visible');
      return(<Redirect to='DailyPrayer'/>)
    }
    this.toggleTabbar('hidden')
    return (
      <IonPage id='onboarding'>
        <Header title='OnBoarding' enableBack={false} />
        <IonContent fullscreen className='onboarding'>
          <IonSlides pager={true} options={slideOpts}>
            <IonSlide>
              <IonGrid>
                <IonRow class="ion-justify-content-center ion-padding-bottom onboard-image">
                  <div>
                    <IonImg src='assets/images/logo-blue.png' />
                  </div>
                </IonRow>
                <IonRow class="ion-justify-content-center">
                  <div className='ion-text-center'>
                    <h1 className='onboard-title' color='blue'>Welcome</h1>
                    <p className='onboard-text'>The ionic conference app is a practical preview of the ionic framework in action, and a demonstration of proper code use.</p>
                  </div>
                </IonRow>
              </IonGrid>
            </IonSlide>
            <IonSlide>
              <IonGrid>
                <IonRow class="ion-justify-content-center ion-padding-bottom onboard-image">
                  <div>
                    <IonImg src='assets/images/notifications.png' />
                  </div>
                </IonRow>
                <IonRow class="ion-justify-content-center">
                  <div className='ion-text-center'>
                    <h1 className='onboard-title'>Notifications</h1>
                    <p className='onboard-text'>The ionic conference app is a practical preview of the ionic framework in action, and a demonstration of proper code use.</p>
                  </div>
                </IonRow>
              </IonGrid>
            </IonSlide>
            <IonSlide>
              <IonGrid>
                <IonRow class="ion-justify-content-center ion-padding-bottom onboard-image">
                  <div>
                    <IonImg src='assets/images/pray.png' className='ion-margin-horizontal'/>
                  </div>
                </IonRow>
                <IonRow class="ion-justify-content-center">
                  <div className='ion-text-center'>
                    <h1 className='onboard-title'>Pray</h1>
                    <p className='onboard-text'>The ionic conference app is a practical preview of the ionic framework in action, and a demonstration of proper code use.</p>
                  </div>
                </IonRow>
              </IonGrid>
            </IonSlide>
            <IonSlide>
              <IonGrid>
                <IonRow class="ion-justify-content-center ion-padding-bottom onboard-image">
                  <div>
                    <IonImg src='assets/images/study.png' />
                  </div>
                </IonRow>
                <IonRow class="ion-justify-content-center">
                  <div className='ion-text-center'>
                    <h1 className='onboard-title'>Study</h1>
                    <p className='onboard-text'>The ionic conference app is a practical preview of the ionic framework in action, and a demonstration of proper code use.</p>
                  </div>
                  </IonRow>
                  <IonRow class="ion-justify-content-center">
                    <IonButton onClick={ () => this.performOnboardingCompletion() }>
                      Lets Begin
                    </IonButton>
                  </IonRow>
              </IonGrid>
            </IonSlide>
          </IonSlides>
        </IonContent>
      </IonPage>
    );
  }
}

export default OnBoarding;
